import React, { useCallback } from 'react';
import './weather-item.css';

interface WeatherProps {
    id: number,
    name: string,
    abbr: string,
    dayName: string,
    dayNumber: number,
    monthName: string,
    maxTemp: number,
    minTemp: number,
    selectDay: any,
}

const WeatherItem = ({ id, name, abbr, dayName, dayNumber, monthName, maxTemp, minTemp, selectDay }: WeatherProps) => {
    const onClickHandler = useCallback(() => {
        selectDay(id)
    }, [selectDay, id]);

    return (
        <li className={`weather-item weather-bg-${abbr}`} onClick={onClickHandler}>
            <div className="weather-item__date">
                <div>{dayName}</div>
                {`${dayNumber} ${monthName}`}
            </div>
            <div className="weather-item__temp">{`${maxTemp}° / ${minTemp}°`}</div>
            <div className="weather-item__icon">
                <img
                    className="weather-item__image"
                    src={`https://www.metaweather.com/static/img/weather/${abbr}.svg`}
                    alt={name}
                />
            </div>
        </li>
    );
};

export default WeatherItem;
