import React from 'react';
import './weather-card.css';

interface WeatherCardProps {
    id: number
    name: string,
    abbr: string,
    dayName: string,
    dayNumber: number,
    monthName: string,
    maxTemp: number,
    minTemp: number,
}

const WeatherCard = ({name, abbr, dayName, dayNumber, monthName, maxTemp, minTemp}: WeatherCardProps) => {
    return (
        <div className={`weather-card weather-bg-${abbr}`}>
            <div className="weather-card__icon">
                <img
                    className="weather-card__image"
                    src={`https://www.metaweather.com/static/img/weather/${abbr}.svg`}
                    alt={name}
                />
            </div>
            <div className="weather-card__temp">{`${maxTemp}° / ${minTemp}°`}</div>
            <div className="weather-card__date">
                <div>{dayName}</div>
                {`${dayNumber} ${monthName}`}
            </div>
        </div>
    );
};

export default WeatherCard;
